<template>
    <base-modal v-show="isModalVisible" @close="closeModal" >
        <div slot="header" class="dv_modal">
            <div class="title_header">Adicionar bolsa</div>
            <div class="subtitle_header">Filtre e adicione as bolsas de seu interesse.</div>
        </div>
        <div slot="body">
            <form class="formulario" action="">
                <label id="lbl_cidade" class="form_label" for="sel_cidade">SELECIONE SUA CIDADE</label>
                <select name="" id="sel_cidade" @change="onChangeCidade($event)" >
                    <option :key="0" value="0" selected >(Escolha sua cidade)</option>
                    <option v-for="cidade in cidades" :key="cidade" :value="cidade">{{ cidade }}</option>
                </select>
                <label id="lbl_curso" class="form_label" for="sel_curso">SELECIONE O CURSO DE SUA PREFERÊNCIA</label>
                <select name="" id="sel_curso" @change="onChangeCurso($event)">
                    <option :key="0" value="0" selected >(Escolha seu curso)</option>
                    <option v-for="curso in cursos" :key="curso">{{ curso }}</option>
                </select>
                <label id="lbl_modalidade" class="form_label" for="">COMO VOCÊ QUER ESTUDAR?</label>
                <div id="dv_espaco"></div>
                <div id="chk_modalidade">
                    <label for="chk_presencial"><input type="checkbox" name="presencial" value="Presencial" id="chk_presencial" @click="onClickPresencial">Presencial</label>                            
                    <label for="chk_distancia"><input type="checkbox" name="distancia" value="EaD" id="chk_distancia" @click="onClickDistancia">A distancia</label>                            
                </div>
                <label id="lbl_valor" class="form_label" for="">ATÉ QUANTO PODE PAGAR?</label>
                <span id="spn_range_valor">{{inputValor}}</span>
                <input type="range" name="valor" id="ipt_range_valor" min="100" max="10000" v-model="inputValor">
            </form>

            <div id="dv_header_cursos" >
                <div style="textAlign: left">
                    Resultado:     
                </div>
                <div style="textAlign: right">
                    Ordenar por <div id="dv_ordenacao" >Nome da Faculdade <font-awesome-icon icon="angle-down" /></div>
                </div>
            </div>

            <div id="dv_cursos" class="tabela">
                <div v-for="(bolsa, i) in bolsas" :key="i" class="linha"> 
                    <div class="celula_checkbox">
                        <input type="checkbox" :value="bolsa" v-model="checkedBolsas" class="dv_cursos_checkbox"  />
                    </div>
                    <div class="celula_logo">
                        <img class="logo_table" :src="bolsa.university.logo_url"/>
                    </div>
                    <div class="celula_curso">
                        <div class="text_main_verde">{{bolsa.course.name}}</div>
                        <div class="text_complement_left">{{bolsa.course.level}}</div>
                    </div>
                    <div class="celula_valor">
                        <div class="text_main">{{"Bolsa de "+bolsa.discount_percentage+"%"}}</div>
                        <div class="text_main">{{"R$ "+bolsa.price_with_discount+"/mês"}}</div>
                    </div>
                </div>
                <div v-if="bolsas.length==0">
                    <div class="title_header flex_center" style="padding-top: 3.125em;" >
                        Nenhuma bolsa encontrada
                    </div> 
                </div>
            </div>
        </div>
        <div slot="footer">
            <button class="btn btn_branco gap_horizontal" id="btn_cancelar" @click="closeModal" >Cancelar</button>
            <button id="btn_adicionar" v-bind:class="['btn', checkedBolsas.length>0?'btn_amarelo':'btn_indisponivel', 'gap_horizontal']" @click="onClickAdicionarBolsas" >Adicionar bolsa(s)</button>
        </div>

    </base-modal>
</template>
<script>
import store from '../store'
export default{
  data(){
    return{
        isModalVisible: false,
        selectedCidade: '0',
        selectedCurso: '0',
        checkedPresencial: false,
        checkedDistancia: false,
        inputValor: 1500,
        checkedBolsas: [],
    }
  },
  created(){
      store.dispatch('load-bolsas')
      store.dispatch('load-bolsas-favoritas')
  },
  methods:{
    showModal(){
      this.isModalVisible = true;
    },
    closeModal(){
      this.isModalVisible = false;
    },
    onChangeCidade(event){
        this.selectedCidade = event.target.value
    },
    onChangeCurso(event){
        this.selectedCurso = event.target.value
    },
    onClickPresencial(){
        this.checkedPresencial = !this.checkedPresencial
    },
    onClickDistancia(){
        this.checkedDistancia = !this.checkedDistancia
    },
    onClickAdicionarBolsas(){
        this.checkedBolsas.map(item => {
            store.dispatch('add-bolsa-favorita', item)
        })
    }
  },
  computed:{
      bolsasFavoritas(){
        return store.state.bolsasFavoritas
      },
      bolsas(){
            let bolsasFiltradas = store.state.bolsas
            if(this.selectedCidade !== '0'){
                bolsasFiltradas = bolsasFiltradas.filter(b => b.campus.city==this.selectedCidade)
            }
            if(this.selectedCurso !== '0'){
                bolsasFiltradas = bolsasFiltradas.filter(b => b.course.name==this.selectedCurso)
            }
            if(this.checkedPresencial){
                bolsasFiltradas = bolsasFiltradas.filter(b => b.course.kind === "Presencial")
            }
            if(this.checkedDistancia){
                bolsasFiltradas = bolsasFiltradas.filter(b => b.course.kind === "EaD")
            }
            let bolsasFavoritas = this.bolsasFavoritas
            
            if(bolsasFavoritas.length > 0){
                bolsasFiltradas = bolsasFiltradas.filter(b => {
                    let retorno = true
                    bolsasFavoritas.forEach(b2=>{
                        if(JSON.stringify(b) === JSON.stringify(b2)){
                            retorno = false
                            return
                        }
                    })
                    return retorno
                })
            }
            return bolsasFiltradas.filter(b => b.price_with_discount <= this.inputValor)
      },
      cidades(){
          return store.getters.cidades
      },
      cursos(){
          return store.getters.cursos
      },
  }
}
</script>
