import Vue from 'vue'
import Vuex from 'vuex'
import scholarships from './services/scholarships'

Vue.use(Vuex)
Vue.use(scholarships)

const state = {
    bolsas: [],
    bolsasFavoritas: []
}

const getters = {
    cidades: state => {
        return [...new Set(state.bolsas.map(bolsa => bolsa.campus.city))].sort((a, b)=>(a>b)?1:-1)
    },
    cursos: state => {
        return [...new Set(state.bolsas.map(bolsa => bolsa.course.name))].sort((a, b)=>(a>b)?1:-1)
    },
    semestres: state => {
        return [...new Set(state.bolsas.map(bolsa => bolsa.enrollment_semester))].sort((a, b)=>(a>b)?1:-1)
    }
}

const mutations = {
    'set-bolsas'(state, bolsas){
        state.bolsas = bolsas.sort((a,b)=>{
            if(a.university.name<b.university.name){
                return -1;
            }
            if(a.university.name>b.university.name){
                return 1;
            }
            return (a.course.name<b.course.name)?-1:1
        });
    },
    'set-bolsas-favoritas'(state, bolsas){
        state.bolsasFavoritas = bolsas
    },
    'add-bolsa-favorita'(state, bolsa){
        state.bolsasFavoritas.push(bolsa)
    },
    'del-bolsa-favorita'(state, bolsa){
        let i = state.bolsasFavoritas.findIndex(b=>b===bolsa)
        if(i>-1){
            state.bolsasFavoritas.splice(i, 1)
        }
    }
}

const actions = {
    'load-bolsas'(context){
        scholarships.getBolsas().then(response => {
            context.commit('set-bolsas', response)
        })
    },
    'load-bolsas-favoritas'(context){
        let bolsas = []
        if (localStorage.getItem("cursos_favoritos") === null) {
            localStorage.setItem("cursos_favoritos", JSON.stringify([]));
        }else{
            bolsas = JSON.parse(localStorage.getItem("cursos_favoritos"))
        }
        context.commit('set-bolsas-favoritas', bolsas)
    },
    'add-bolsa-favorita'(context, bolsa){
        if(!state.bolsasFavoritas.includes(bolsa)){
            context.commit('add-bolsa-favorita', bolsa)
            localStorage.setItem("cursos_favoritos", JSON.stringify(state.bolsasFavoritas));
        }
    },
    'del-bolsa-favorita'(context, bolsa){
        if(state.bolsasFavoritas.includes(bolsa)){
            context.commit('del-bolsa-favorita', bolsa)
            localStorage.setItem("cursos_favoritos", JSON.stringify(state.bolsasFavoritas));
        }
    }
}

export default new Vuex.Store({
    state,
    mutations,
    actions,
    getters
})