<script>
import store from '../store'
export default {
  name: 'Home',
  data(){
    return{
      isModalVisible: false,
      selectedSemestre: '0'
    }
  },
  created(){
      store.dispatch('load-bolsas-favoritas')
  },
  methods:{
    showModal(){
      this.getModal().showModal()
    },
    closeModal(){
      this.getModal().closeModal()
    },
    getModal(){
        return this.$refs.modal;
    },
    changeSemester(sem){
      this.selectedSemestre = sem
    },
    removeFromFavorites(bolsa){
      store.dispatch('del-bolsa-favorita', bolsa)
    }
  },
  computed:{
    bolsasFavoritas(){
      let bolsasFiltradas = store.state.bolsasFavoritas
      if(this.selectedSemestre !== '0'){
          bolsasFiltradas = bolsasFiltradas.filter(b => b.enrollment_semester==this.selectedSemestre)
      }
      return bolsasFiltradas
    },
    semestres(){
      return store.getters.semestres
    }
  }
}
</script>

