import Vue from 'vue'
import App from './App.vue'

import { library } from '@fortawesome/fontawesome-svg-core'
import { faAngleDown, faAngleLeft, faPlus, faInfo } from '@fortawesome/free-solid-svg-icons'
import { faUserCircle, faComments, faEnvelope, faHeart, faCircle } from '@fortawesome/free-regular-svg-icons'
import { faWhatsapp } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon, FontAwesomeLayers } from '@fortawesome/vue-fontawesome'

import BaseModal  from './components/BaseModal.vue'
import FiltroBolsasModal from './components/FiltroBolsasModal.vue'
import store from './store'

library.add(faWhatsapp, faUserCircle, faAngleDown, faAngleLeft, 
  faComments, faEnvelope, faHeart, faCircle, faPlus, faInfo);

Vue.component('font-awesome-icon', FontAwesomeIcon)
Vue.component('base-modal', BaseModal)
Vue.component('filtro-bolsas-modal', FiltroBolsasModal)
Vue.component('font-awesome-layers', FontAwesomeLayers)

Vue.config.productionTip = false

new Vue({
  render: h => h(App),
  store,
}).$mount('#app')
